@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700;800;900&display=swap');

@layer base {
    body{
        @apply font-[Raleway];
    }
    li {
        @apply px-4;
        @apply cursor-pointer
    }
}

.full-height {
	height: 100vh;
}

.flex-grow{
	display: flex;
	align-items: center;
	justify-content: center;
}

.hidden {
	display: none;
}

/* ------------------------------------------------------ */

.projet-div {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 250px;
}

.projet-div:hover {
    background-image: linear-gradient(
    to right,
	#586930ec,
    #5fac69e3
    )!important;
}

/* --------------------------------------------------------------- */

.thib{
	background: linear-gradient( 
		#5f8661a1, transparent);
		width: 21rem;
		height: 21rem;
		left: calc(50% - 11rem);
		border-radius: 100%;
		overflow: hidden;
	}

	@media screen and (max-width: 800px){
		.thib{
			width: 20rem;
			height: 20rem;
		}
		}
		@media screen and (max-width: 740px){
			.thib{
				width: 15rem;
				height: 15rem;
			}
			}

	@media screen and (max-width: 685px){
		.profil-left-container{
		display: none;
		}
	}

	@media screen and (min-width: 685px){
				.profil-mobile-button{
					display: none;
				}
		}

/* ----------------------------------------------------------------- */


		.button{
			background-color: #121614;
			box-shadow:0 20px 50px rgba(0, 0, 0, 0.897);
			overflow:hidden;
			position: relative;
			text-decoration:none;
			color: #fff;
		}
		.button:hover{
				color:#0dd35f;	
				text-decoration: underline;
				text-decoration-thickness: 2px;
			}

			.button:before
			{
			content: '';
			position: absolute;
			top: 2px;
			left: 2px;
			bottom: 2px;
			width: 50%;
			}

			.button span:nth-child(1)
			{
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 2px;
			background: linear-gradient(to right,#17221C,#07ed66);
			animation: animate1 2.5s linear infinite;
			}
			@keyframes animate1
			{
			 0%
			 {
			   transform:translateX(-100%);
			 
			 }
			 100%
			 {
			   transform:translateX(100%);
			 
			 }
			}
			.button span:nth-child(2)
			{
			position: absolute;
			top: 0;
			right: 0;
			width: 2px;
			height: 100%;
			background: linear-gradient(to bottom,#17221C,#07ed66);
			animation: animate2 2.5s linear infinite;
			animation-delay: 1.5s;
			}
			@keyframes animate2 
			{
			 0%
			 {
			   transform:translateY(-100%);
			 
			 }
			 100%
			 {
			   transform:translateY(100%);
			 
			 }
			}
			.button span:nth-child(3)
			{
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 2px;
			background: linear-gradient(to left,#17221C,#07ed66);
			animation: animate3 2.5s linear infinite;
			}
			@keyframes animate3
			{
			 0%
			 {
			   transform:translateX(100%);
			 
			 }
			 100%
			 {
			   transform:translateX(-100%);
			 
			 }
			}
			.button span:nth-child(4)
			{
			position: absolute;
			top: 0;
			left: 0;
			width: 2px;
			height: 100%;
			background: linear-gradient(to top,#17221C,#07ed66);
			animation: animate4 2.5s linear infinite;
			animation-delay: 1.5s;
			}
			@keyframes animate4 
			{
			 0%
			 {
			   transform:translateY(100%);
			 
			 }
			 100%
			 {
			   transform:translateY(-100%);
			 
			 }
			}


/* ---- */
.rightNavButton:hover .rightNavArrow {
	transform: translateX(10px);
}
.rightNavArrow {
	transition: transform 0.2s ease-in-out;
}

.leftNavButton:hover .leftNavArrow {
	transform: translateX(-10px);
}
.leftNavArrow {
	transition: transform 0.2s ease-in-out;
}
